import React, { Component, Suspense } from 'react'
import QRCode from 'react-qr-code';
import CodeShareCopy from './CodeShareCopy'
import { TINY_URL } from '../../../../../../../actions/api'
import { WASABI_PATH } from '../../../../../../../actions/api';
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '../../../../../../../Assets/AssetsHelper'
import ShareCodeModal from '../../../../CommonComponents/ShareCodeModal';
import { Button, Image, Collapse, OverlayTrigger, Tooltip, Form } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD, IMAGE_DOWNLOAD } from '../../../../../../../utils';

export const NumberBullet = ({ count }) => <span className='d-inline-block text-center h-20px w-20px rounded-circle me-2 bg-primary text-white fs-9 flex-shrink-0' style={{ lineHeight: '20px' }}>{count}</span>
export default class Publish extends Component {

    state = { codeCopied: false, shareUrlModal: false, isUrl: false }

    codeCopy = (id) => {
        this.setState({ codeCopied: true })
        setTimeout(() => {
            this.setState({ codeCopied: false })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }

    shareUrlCallback = () => {
        this.setState({ shareUrlModal: false, isUrl: false })
    }

    shareUrlOpenModal = (url) => this.setState({ shareUrlModal: true, isUrl: url ? true : false })

    shareLinkOnNetworks = (key) => {
        const { tinyUrl, title } = this.props
        switch (key) {
            case 'facebook':
                return window.open(`https://www.facebook.com/sharer/sharer.php?u=${tinyUrl}&t=${title}&quote=','${title}','width=600,height=400`)
                break;
            case 'twitter':
                return window.open(`https://twitter.com/intent/tweet?text=${title} ${tinyUrl}&related=UGC,micropat','${title}','width=600,height=400`)
                break;
            case 'linkedin':
                return window.open(`https://www.linkedin.com/shareArticle/?mini=true&url=${tinyUrl}&title=${title}&summary=&source=TaggboxShare','${title}','width=600,height=400`)
                break;
            default:
                break;
        }
    }

    render() {
        const { tinyUrl, qrStatus, buttonText, fontSize, bgColor, uniqueCode, fcColor, showEmbed } = this.props
        const { codeCopied, shareUrlModal, isUrl } = this.state
        const QRImage = `${WASABI_PATH}onsiteupload/images/${uniqueCode}.png`
        return (
            <div className='p-4'>
                <p className="h5 mb-2 d-flex"><NumberBullet count={1} />Collect and show shared content.</p>
                <div className='ps-8 mb-5'>
                    <p className="mb-2">Feel free to share this URL with your audience, enabling them to upload visual user-generated content (UGC) and reviews.</p>
                    <p className="mb-5">Get creative with the UGC on your marketing touchpoints to delight your audience.</p>
                    <div className="d-flex align-items-center w-100">
                        <div className="position-relative w-100">
                            <Form.Control type="text" name="ShareURL" id="ShareURL" size="lg" placeholder="ShareURL" onClick={() => this.codeCopy('ShareURL')} value={tinyUrl} style={{ paddingRight: 40 }} />
                            <div>
                                <Button variant='light' size='lg' className="position-absolute top-0 text-gray-700 snapUp_embed_copy bg-transparent border-0 tranform-middle px-2 fs-6" style={{ right: 5 }} onClick={() => this.codeCopy('ShareURL')} aria-label='copy' title='copy'>
                                    {!codeCopied ?
                                        <FontAwesomeIcon type="solid" icon={'copy'} />
                                        :
                                        <FontAwesomeIcon type="solid" icon={'check'} iconClass={'text-success'} />
                                    }
                                </Button>
                            </div>
                        </div>
                    </div>
                </div>
                <p className="h5 mb-2 d-flex"><NumberBullet count={2} />Share Your Campaign URL Here:</p>
                <div className='ps-8 mb-5'>
                    <p className="mb-2">Easily share this URL across multiple platforms like Facebook, Twitter, Linkedin or email to engage your audience with visual user-generated content (UGC) and reviews.</p>

                    <div className='d-flex align-items-center'>
                        <Link className='m-2 text-gray-500 fs-6 active-dark' onClick={() => this.shareLinkOnNetworks('facebook')} title='facebook'>
                            <FontAwesomeIcon type="brands" icon={'facebook'} />
                        </Link>

                        <Link className='m-2 text-gray-500 fs-6 active-dark' onClick={() => this.shareLinkOnNetworks('twitter')} title='twitter'>
                            <FontAwesomeIcon type="brands" icon={'x-twitter'} />
                        </Link>

                        <Link className='m-2 text-gray-500 fs-6 active-dark' onClick={() => this.shareLinkOnNetworks('linkedin')} title='linkedin'>
                            <FontAwesomeIcon type="brands" icon={'linkedin-in'} />
                        </Link>

                        <Link className='m-2 text-gray-500 fs-6 active-dark' onClick={() => this.shareUrlOpenModal('url')} title='url'>
                            <FontAwesomeIcon type="solid" icon={'envelope'} />
                        </Link>
                    </div>
                </div>
                <p className="h5 mb-2 d-flex"><NumberBullet count={3} />Download Or Embed QR:</p>
                <div className='ps-8 mb-5'>
                    {/* <div className='d-flex justify-content-sm-start justify-content-center flex-wrap flex-sm-nowrap p-4 bg-light rounded-3 box-shadow' style={{ maxWidth: 500 }}> */}
                    <div className='d-flex justify-content-sm-start justify-content-center flex-wrap p-4 bg-light rounded-3 box-shadow' style={{ maxWidth: 500 }}>
                        <div className="d-flex flex-wrap">
                            <div className='d-flex flex-column flex-shrink-0 mx-auto mb-2' style={{ width: 100 }}>
                                {!uniqueCode ? <QRCode
                                    level="L"
                                    size={100}
                                    value={tinyUrl}
                                /> :
                                    <Image src={QRImage} height={100} width={100} alt='QR' fluid />
                                }
                            </div>
                            <div className='d-flex text-center flex-column'>
                                <p className="mb-3 fs-8 text-center">Easily download this QR code or copy the embed code to share it on your platforms.</p>
                                {/* <div className='d-flex align-items-center flex-wrap'>
                                        <Button variant='dark' className='m-1 text-nowrap' onClick={()=>IMAGE_DOWNLOAD(QRImage, 'QR-Code')}>Download QR</Button>
                                        <Button variant='secondary' className='m-1' onClick={()=> this.props.onShowEmbed()}>Embed QR</Button>
                                    </div> */}
                            </div>
                        </div>
                        <div className='d-flex flex-wrap justify-content-center w-100'>
                            <Button variant='dark' className='m-1 text-nowrap px-3 px-md-2 px-sm-2' onClick={() => IMAGE_DOWNLOAD(QRImage, 'QR-Code')}>Download QR</Button>
                            <Button variant='secondary' className='m-1 px-2' onClick={() => this.props.onShowEmbed()}>Embed QR</Button>
                        </div>
                    </div>
                </div>
                <div className="ps-8 mb-5">
                    <Collapse in={showEmbed}>
                        <div id="example-collapse-text">
                            <CodeShareCopy copyText={
                                `<style>		
                                .tb_onsite_upload_btn_wrap {padding: 10px;}
                                .tb_onsite_upload_btn,
                                .tb_onsite_upload_btn_wrap {align-items: center;display: flex;justify-content: center}
                                .tb_onsite_upload_btn {flex-direction: column;max-width: 350px}
                                .tb_onsite_btn_txt {align-items: center;display: flex;font-size: 14px;font-weight: 400;padding: 0 10px}
                                .tb_onsite_btn {background-color: ${bgColor};border-radius: 3px;color: #fff;cursor: pointer;font-weight: 500;padding: 6px;text-decoration:none;}
                                .tb_onsite_btn:hover,.tb_onsite_btn:focus {text-decoration:none;}
                                .tb_onsite_btn,
                                .tb_onsite_qr_btn_wrap {align-items: stretch;display: flex;justify-content: center}
                                .tb_onsite_qr_code {align-items: center;background: #fff;display: flex;justify-content: center;object-fit: cover;padding: 0px}
                                </style> 
                                <div class="tb_onsite_upload_btn_wrap">
                                <div class="tb_onsite_upload_btn">
                                <a href="${tinyUrl}" target="_blank" style="background-color: ${bgColor}" class="tb_onsite_btn" title='Qr'>
                                <div class="tb_onsite_qr_btn_wrap"> 
                                ${qrStatus ? `<div class="tb_onsite_qr_code"> <img src="${QRImage}" height="60" width="60" /> </div>` : ""}
                                <div class="tb_onsite_btn_txt" style="color:${fcColor};font-size:${fontSize}px;text-align:center;">${buttonText}</div>
                                </div>
                                </a>
                                </div>
                                </div>`}
                                id={'buttonEmbedCode'} height={6} onClickToOpen={this.shareUrlOpenModal} type="2" readOnly={true} />
                        </div>
                    </Collapse>
                </div>


                <Suspense fallback={<></>}>
                    {shareUrlModal ? <ShareCodeModal text="Share this embed code with your team. Enter the email Id to send the code directly to them" heading="Share Embed Code" onClose={this.shareUrlCallback} codeId={isUrl ? '#ShareURL' : '#buttonEmbedCode'} shareUrl={tinyUrl} from={'reviewHub'} /> : null}
                </Suspense>
            </div>
        )
    }
}

